.App {
  text-align: center;
}

.App-logo {
  height: 50px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.stations {
  padding: 2em;
  text-align: center;
}

.station-container {
  display: inline-block;
  text-align: left;
}

.station {
  padding: 1em;
  font-size: 1.5em;
}

.logo {
  padding-right: .5em;
  vertical-align: bottom;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



